import rainDay from '../../img/rain-day.jpg';
import rainNight from '../../img/rain-night.jpg';
import clearDay from '../../img/clear-day.jpg';
import clearNight from '../../img/clear-night.png';
import cloudyDay from '../../img/cloudy-day.png';
import cloudyNight from '../../img/cloudy-night.jpg';
import mistDay from '../../img/mist-day.jpg';
import mistNight from '../../img/mist-night.jpg';
import snowDay from '../../img/snow-day.jpg';
import snowNight from '../../img/snow-night.jpg';
import WeatherConditionView from "./weatherConditionView.js";

class HeroView extends WeatherConditionView {
    _parentElement = document.querySelector('.hero');
    _icons = {
        rainDay,
        rainNight,
        clearDay,
        clearNight,
        cloudyDay,
        cloudyNight,
        mistDay,
        mistNight,
        snowDay,
        snowNight,
    };
    constructor() {
        super();
    }

    _generateMarkup(data) {
        return `
            <img
              class="hero__img"
              src="${this.setIconByCondition(data.condition, data.is_day)}"
              alt="${data.condition}"
            />
        `;
    }
}

export default new HeroView();